// 引入
@import "./_include/_include.sass";

$mobile: "screen and (max-width: 900px)";
$mini: "screen and (max-width: 640px)";

* {
  vertical-align: middle;
  font-family: 'Microsoft JhengHei'; }

h1,h2,h3,h4,h5,h6,p {
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  li {
    list-style-type: none; } }

[data-bg] {
  display: block;
  @include size(100%,auto);
  background-size: cover;
  background-repeat: no-repeat; }

.flex-row {
  display: flex;
  flex-wrap: wrap; }



#wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100vh;
  padding: 40px 0;
  // background-image: url('../images/background.jpg')
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    @include size(100%,100%);
    background-color: #333;
    background-image: url('../images/background.jpg');
    background-size: cover;
    background-position: left 0;
    opacity: 0.5; } }


.container {
  padding: 50px;
  width: 95%;
  max-width: 800px;
  position: relative;
  z-index: 2;
  background-color: #fff;
  border-radius: 20px;
  opacity: 0;
  @media #{$mobile} {
    padding: 50px 20px; }
  &:before {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    border: solid 1px red;
    border-radius: 20px;
    @include size(calc(100% - 30px));
    opacity: 0.3; }
  &:after {
    content: "";
    position: absolute;
    background-image: url('../images/cardbg.jpg');
    background-size: cover;
    background-position: center;
    top: 0;
    left: 0;
    @include size(100%);
    opacity: 0.1; } }

.opening {
  // display: none
  position: fixed;
  top: 0;
  left: 0;
  @include size(100%);
  z-index: 10;
  background-color: rgba(#fff,.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 20px 100px;
  span {
    // visibility: hidden
    display: none; }
  p {
    line-height: 30px;
    font-size: 20px;
    font-weight: bold;
    @media #{$mini} {
      font-size: 16px; } } }
#wrapper {
  h2.logo {
    margin: 0 auto;
    @include size(50%,auto);
    max-width: 200px;
    img {
      display: block;
      @include size(100%,auto); } } }

.wedding-info {
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
  z-index: 3;
  p,h3,li span,a {
    font-weight: bold; }
  p,span {
    @include cr(rgba(red,.5));
    font-size: 18px;
    @media #{$mobile} {
      font-size: 16px; } }
  p,h3 {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    line-height: 1;
    justify-content: center;
    font {
      display: block;
      margin-right: 10px; }
    i {
      display: block;
      font-size: 14px;
      @include cr(#999);
      font-style: inherit;
      margin-left: 5px;
      @media #{$mobile} {
        font-size: 12px; }
      @media #{$mini} {
        display: none; } } }
  h3 {
    font-size: 30px;
    @media #{$mobile} {
      font-size: 20px; } }
  p+h3 {
    margin-top: 15px; }
  li {
    margin-bottom: 15px; }
  li span {
    display: block;
    text-align: center;
    margin-bottom: 10px; }
  span+p,span+a {
    display: flex;
    justify-content: center;
    @include cr(#000);
    font-size: 18px;
    @media #{$mobile} {
      font-size: 16px; } } }

#googleForm {
  display: none;
  position: fixed;
  overflow: scroll;
  top: 0;
  z-index: 10;
  left: 0;
  @include size(100%);
  justify-content: center;
  background-color: rgba(#000,.5);
  #close {
    position: fixed;
    top: 5px;
    right: 5px;
    @include size(25px);
    background-color: transparent;
    border: none;
    background-image: url(../images/cancel.svg);
    background-size: cover; }
  iframe {
    background-color: #fff;
    padding: 15px 0;
    max-width: 100%;
    height: 100%;
    margin: 0 auto;
    display: table; } }

#go-google-form {
  display: table;
  padding: 10px 30px;
  border-radius: 50px;
  cursor: pointer;
  z-index: 10;
  position: relative;
  margin: 0 auto;
  border: solid 1px rgba(red,.5);
  @include cr(rgba(red,.5));
  &:after {
    content: "";
    display: block;
    position: absolute;
    right: 85%;
    bottom: 40%;
    @include size(40px);
    background-image: url('../images/rose.png');
    background-size: contain; } }

#start {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 100;
  border: none;
  background-color: rgba(red,.5);
  @include size(110px);
  text-align: center;
  padding: 0 0 0 1px;
  border-radius: 50%;
  @include cr(#fff);
  letter-spacing: 3px;
  cursor: pointer;
  &:after,&:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    @include size(100%);
    border-radius: 50%;
    border: 1px solid rgba(red,.5); }
  &:after {
    animation: fade2 2s infinite; }
  &:before {
    animation: fade2 2s .5s infinite; } }


@keyframes fade2 {
  0% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(1.3);
    opacity: 0; } }

